export default {
	data() {
		return {
			list: [2, 2, 3, 4, 7, 8],
			lists: []
		};
	},
	onLoad(options) {
		console.log(options)
		this.getList(options.id)
	},
	methods: {
		getList(id) {
			this.$http.request({
				url: this.$api.CatalogCurrent,
				data: {
					categoryId: id
				}
			}).then(res => {
				console.log(123)
				console.log("res: " + JSON.stringify(res));
				let data = res.data;
				this.lists = data.records;
			})
		},
		goDetail(id){
			uni.navigateTo({
				url:"../productDetail/productDetail?id=" + id
			})
		}
	}
}
